<template>
  <div id="shareIt">
    <!-- 在微信环境下弹出分享引导层 -->
     <div v-if="wechatisshow" style="z-index: 3" class="wechat-cotent"  @click = 'closeShare'>
        <div class="">
          <h1 class="step1">
            第一步：点击右上角的...
            <img src="../../assets/img/dot.png" class="dot-img" alt="">
          </h1>
          <div class="jiantou">
            <img class="wp100" src="../../assets/img/jiantou.png" alt="">
          </div>
          <div class="imgIcon">
           <h1 class="step2">第二步：选择分享方式进行分享</h1>
           <div class="wechat-icon">
             <div class="wechat-icon-img">
              <img src="../../assets/img/friend.png" class="wp100">
             </div>
             <div class="wechat-icon-img">
              <img src="../../assets/img/wechat.png" class="wp100" alt="">
             </div>
             <div class="wechat-icon-img">
              <img src="../../assets/img/qq.png" class="wp100" alt="">
             </div>
           </div>
          </div>
        </div>
     </div>
     <!-- 不在微信环境下，但是在APP环境下,引导分享的方式 -->
      <div class='appShare' v-if="$config.source_style >= 6" @click='closeShare'>
        <transition name='disappear'>
          <div class="app-content" v-if="appisshow" @click.stop='""'>
            <div class="topShareTitle">
              <div class="share_img"></div>
            </div>
            <div class="topshow">
              <div class="icon" @click="setShare('weixin','WXSceneTimeline')">
                <div class="iconBox">
                  <div class="iconImg">
                    <img src="../../assets/img/friend.png" alt="">
                  </div>
                  <div class="name">朋友圈</div>
                </div>
              </div>
              <div class="icon" @click="setShare('weixin','WXSceneSession')">
                <div class="iconBox">
                  <div class="iconImg">
                    <img src="../../assets/img/wechat.png" alt="">
                  </div>
                  <div class="name">微信</div>
                </div>
              </div>
              <div class="icon" @click="setShare('qq')">
                <div class="iconBox">
                  <div class="iconImg">
                    <img src="../../assets/img/qq.png" alt="">
                  </div>
                  <div class="name">QQ</div>
                </div>
              </div>
            
            </div>
            <div class="canlehid" @click="closeShare">取消</div>
          </div>
        </transition>
      </div>
  </div>
</template>

<script>
// 导入wx 分享接口对象
import wx from 'weixin-js-sdk'
// import Clipboard from 'clipboard'
export default {
  name: 'shareIt',
  props: {
    details: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      isShowShare: true,
      appisshow: false, // 控制在APP环境下要不要分享到朋友圈，微信，QQ
      wechatisshow: false, // 控制微信环境下要不要分享到朋友圈，微信，QQ
      pageUrl: '', // 提交给服务器端的URL
      share: null
    }
  },
  methods: {
    // 分享功能
    /** 实现思路：
     * 1.检测有没有登录，如果没有登录，要先登录，然后再返回到当前页，如果已经登录，检测是否在微信环境
     * 2.如果在微信环境中，直接浮层引导分享，（可分享，微信群，朋友圈，QQ）
     * 3.如果非微信环境，直接将带分享人信息参数的文章分享页短连接，复制到剪贴板上，
     * 格式如下：文章标题前15个字：http://t.cn/fefvDAA 带分享人信息参数，并弹出toast提示："本文链接已经复制，快发送给好友吧"
     * 4.分享后，分享方案和描述如下：标题为文章标题，内容为文章内容，同时最前方加入【掘金宝】标识，图片为文章首图(缺省图片为logo)
     *  */
    shareSuccess () {
      console.log('分享之后的地址' + location.href)
      setTimeout(() => {
        console.log('分享之后的地址' + location.href)
      }, 3000)
      this.$emit('shareSuccess')
    },
    // 分享功能
    shareComment () {
      let weChatFlag = this.$utils.isWx()
      if (weChatFlag) {
        // 让分享引导浮层弹出来
        this.wechatisshow = true
      } else { // 如果不在微信环境下
        this.appisshow = true
      }
    },
    // 分享朋友圈
    setShare (id, ext) {
      let contentEl = document.createElement('div')
      contentEl.innerHTML = this.details.content
      let content = contentEl.innerText.substring(0, 50)
      let that = this
      if (!this.share) {
        this.closeShare()
        return this.$toast({
          message: '暂无分享服务',
          position: 'middle',
          duration: 1500,
          iconClass: 'icon icon-error'
        })
      }
      let shareUrl
      if (localStorage.getItem('domain')) {
        this.$config.currentWebUrl = localStorage.getItem('domain')
      }
      shareUrl = this.$config.currentWebUrl + '?path=' +  this.$route.path + '&inviteCode=' + this.$utils.cache('inviteCode')
      let msg = {
        title: this.details.title,
        content,
        thumbs: this.details.img_url && this.details.img_url.length > 0 ? [this.details.img_url[0]] : [`https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}`],
        href: shareUrl
      }
      if (!this.share[id]) {
        this.closeShare()
        return this.$toast({
          message: '暂无分享通道',
          position: 'middle',
          duration: 1500,
          iconClass: 'icon icon-error'
        })
      }
      
      if (ext) {
        msg.extra = {
          scene: ext
        }
      }
      let shareType = 1
      if (id === 'weixin') {
        if (ext === 'WXSceneTimeline') {
        } else {
          shareType = 1
        }
        msg.type = 'web'
      } else {
        shareType = 1
      }
      console.log(shareType)
      if (this.share[id].nativeClient) {
        this.share[id].send(msg, function (res) {
          // 微信分享回调参数
          if (id === 'qq') {
            that.$toast({
              message: '分享成功',
              duration: 1500
            })
          }
          that.shareSuccess()
          that.closeShare()
        }, function (e) {
          if (e.code === -2) {
            that.$toast({
              message: '已取消分享',
              duration: 1500
            })
          } else {
            that.$toast({
              message: '分享失败',
              duration: 1500
            })
          }
          that.closeShare()
        })
      } else {
        let msg = ''
        if (id === 'weixin') {
          msg = '微信'
        } else if (id === 'qq') {
          msg = 'qq'
        }
        that.$toast('请先安装' + msg)
      }
    },
    loadWxSignature () {
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          let sig = res.data
          this.pageUrl = sig.url + `?path=${this.$route.path}&inviteCode=${this.$utils.cache('inviteCode')}`
          let that = this
          let contentEl = document.createElement('div')
          contentEl.innerHTML = this.details.content
          let content = contentEl.innerText
          var shareContent = {
            title: this.details.title,
            desc: content, // 分享描述
            link: this.pageUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: this.details.img_url && this.details.img_url.length > 0 ? this.details.img_url[0] : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
          }
          // 配置微信签名信息
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: sig.appid, // 必填，公众号的唯一标识
            timestamp: sig.timestamp, // 必填，生成签名的时间戳
            nonceStr: sig.nonceStr, // 必填，生成签名的随机串
            signature: sig.signature, // 必填，签名
            jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
          })
          wx.ready(function () {
            // 分享到朋友圈
            wx.onMenuShareTimeline({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function (e) {
                let temp = e.errMsg.split(':')
                if (temp[1] === 'ok') {
                  that.shareSuccess()
                  that.$toast({
                    message: '分享成功',
                    duration: 1500
                  })
                }
                that.closeShare()
              }
            })
            // 分享到给好友
            wx.onMenuShareAppMessage({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function (e) {
                let temp = e.errMsg.split(':')
                if (temp[1] === 'ok') {
                  that.shareSuccess()
                }
                that.closeShare()
              }
            })
            // 分享到qq
            wx.onMenuShareQQ({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.shareSuccess()
                that.closeShare()
              },
              cancel: function () {
                that.closeShare()
              }
            })
          })
          wx.error((res) => {
            console.log(res)
          })
        }
      })
    },
    closeShare () {
      this.isShowShare = false
      this.wechatisshow = false
      this.appisshow = false
      this.$emit('closeShare')
    }
  },
  mounted () {
    const that = this
    this.shareComment() // 调用分享功能
    if (this.$utils.isWx()) {
      this.loadWxSignature()
    }
    var setAppShare = function () {
      that.share = {}
      window.plus.share.getServices(function (s) {
        for (let v of s) {
          that.share[v.id] = v
        }
      })
    }
    if (that.$config.source_style === 6 || that.$config.source_style === 7) {
      if (window.plus) {
        setAppShare()
      } else {
        document.addEventListener('plusready', function () {
          setAppShare()
        })
      }
    }
  },
  destroyed () {
    this.$emit('closeShare')
  }

}

</script>

<style lang="scss" scoped>
#shareIt{
  width: 100%;
  height: 100vh;
  height: 100%;
  .appShare {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 12;
    background: rgba($color: #000000, $alpha: .7);
  }
  .app-content{
    height: 422px;
    position: absolute;
    bottom: 0;
    border: 0;
    width: 750px;
    z-index: 1;
    filter:alpha(opacity=30);
    .topShareTitle{
      height: 100px;
      line-height: 100px;
      color: #fff;
      font-size: 34px;
      text-align: center;
      background: linear-gradient(to right,#fcb164, #f65635);
      position: relative;
      .share_img{
        height: 150px;
        background: url('../../assets/img/share_title.png') no-repeat;
        background-size: 750px 150px;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
      }
    }
    .topshow{
      border:1px solid #ddd;
      background: #dbdbdb;
      padding: 0 115px;
      height: 230px;
      overflow: hidden;
      position: relative;
      text-align: center;
      align-items: center;
      display: flex;
      justify-content: space-around;
      .iconImg{
        width: 110px;
        height: 110px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .name{
        margin-top: 15px;
        font-size: 20px;
      }

    }
    .canlehid{
      height: 90px;
      border-top:1px solid #dbdbdb;
      box-sizing: border-box;
      background: #fff;
      text-align: center;
      bottom: 0;
      position: absolute;
      width: 100%;
      line-height: 80px;
    }
  }
}
.wechat-cotent{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .7);
  width: 100%;
  height: 100%;
  color: #fff;
  font-size: 34px;
  .jiantou{
    width: 206px;
    position: absolute;
    right: 30px;
    top:20px;
  }
  .wechat-icon-img {
    width: 110px;
    height: 110px;
  }
  .step1{
    padding:123px 0 75px 60px;
    display: flex;
    align-items: center;
    align-content: center;
    .dot-img{
      width: 70px;
      margin-left: 20px;
    }
  }
  .step2{
    padding-left: 110px;
    padding-bottom: 60px;
  }
  .wechat-icon{
    width: 484px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}
</style>



